import { Hono } from "hono";
import { cors } from "hono/cors";

import {
  mountMcpRoute,
  mountWidgetAssetsRoute,
  type Env,
} from "./lib/mcp-server";

const app = new Hono<{ Bindings: Env }>();

app.use("*", cors());

app.get("/", (c) => c.text("Pizzaz MCP Hono worker"));

mountMcpRoute(app);
mountWidgetAssetsRoute(app);

export default app;

