import { z } from "zod";

export type PizzazWidget = {
  id: string;
  title: string;
  templateUri: string;
  invoking: string;
  invoked: string;
  htmlAsset: string;
  responseText: string;
};

export type WidgetLookup = {
  byId: Map<string, PizzazWidget>;
  byUri: Map<string, PizzazWidget>;
};

export const toolInputSchema = {
  type: "object",
  properties: {
    pizzaTopping: {
      type: "string",
      description: "表示するウィジェット内で言及するトッピングの名前",
    },
  },
  required: ["pizzaTopping"],
  additionalProperties: false,
} as const;

export const toolInputParser = z.object({
  pizzaTopping: z.string(),
});

const widgetMeta = (widget: PizzazWidget) => ({
  "openai/outputTemplate": widget.templateUri,
  "openai/toolInvocation/invoking": widget.invoking,
  "openai/toolInvocation/invoked": widget.invoked,
  "openai/widgetAccessible": true,
  "openai/resultCanProduceWidget": true,
}) as const;

export const createWidgets = (
  htmlAssetProvider: (componentName: string) => string
) => {
  const widgets: PizzazWidget[] = [
    {
      id: "pizza-map",
      title: "Show Pizza Map",
      templateUri: "ui://widget/pizza-map.html",
      invoking: "Hand-tossing a map",
      invoked: "Served a fresh map",
      htmlAsset: htmlAssetProvider("pizzaz"),
      responseText: "Rendered a pizza map!",
    },
    {
      id: "pizza-carousel",
      title: "Show Pizza Carousel",
      templateUri: "ui://widget/pizza-carousel.html",
      invoking: "Carousel some spots",
      invoked: "Served a fresh carousel",
      htmlAsset: htmlAssetProvider("pizzaz-carousel"),
      responseText: "Rendered a pizza carousel!",
    },
    {
      id: "pizza-albums",
      title: "Show Pizza Album",
      templateUri: "ui://widget/pizza-albums.html",
      invoking: "Hand-tossing an album",
      invoked: "Served a fresh album",
      htmlAsset: htmlAssetProvider("pizzaz-albums"),
      responseText: "Rendered a pizza album!",
    },
    {
      id: "pizza-list",
      title: "Show Pizza List",
      templateUri: "ui://widget/pizza-list.html",
      invoking: "Hand-tossing a list",
      invoked: "Served a fresh list",
      htmlAsset: htmlAssetProvider("pizzaz-list"),
      responseText: "Rendered a pizza list!",
    },
  ];

  const widgetsById = new Map<string, PizzazWidget>();
  const widgetsByUri = new Map<string, PizzazWidget>();

  widgets.forEach((widget) => {
    widgetsById.set(widget.id, widget);
    widgetsByUri.set(widget.templateUri, widget);
  });

  return {
    list: widgets,
    lookup: {
      byId: widgetsById,
      byUri: widgetsByUri,
    } satisfies WidgetLookup,
    metaFor(widget: PizzazWidget) {
      return widgetMeta(widget);
    },
  } as const;
};

export const createResourcePayload = (widget: PizzazWidget) => ({
  uri: widget.templateUri,
  name: widget.title,
  description: `${widget.title} widget markup`,
  mimeType: "text/html+skybridge",
  _meta: widgetMeta(widget),
} as const);

export const createResourceTemplatePayload = (widget: PizzazWidget) => ({
  uriTemplate: widget.templateUri,
  name: widget.title,
  description: `${widget.title} widget markup`,
  mimeType: "text/html+skybridge",
  _meta: widgetMeta(widget),
} as const);

export const createToolPayload = (widget: PizzazWidget) => ({
  name: widget.id,
  description: widget.title,
  inputSchema: toolInputSchema,
  title: widget.title,
  _meta: widgetMeta(widget),
  annotations: {
    destructiveHint: false,
    openWorldHint: false,
    readOnlyHint: true,
  },
} as const);

export const respondWithWidget = (widget: PizzazWidget, topping: string) => ({
  content: [
    {
      type: "text" as const,
      text: widget.responseText,
    },
  ],
  structuredContent: {
    pizzaTopping: topping,
  },
  _meta: widgetMeta(widget),
});

